}
nav ul li:hover {
  background-color: #e0f2f1

}

.circle {
  background: rgba(15, 28, 63, 0.125);
  border-radius: 50%;
  height: 2em;
  object-fit: cover;
  width: 2em;
}
.swiper-container {
  width: auto;
  height: auto;
  position:relative;

}

.swiper-slide img {
  width: 100%;
}
@media screen and (min-width: 640px) {
  .swiper-container {
    width: auto;
  }
}

@media screen and (min-width: 768px) {
  .swiper-container {
    width: auto;
    height: auto;
  }
}
